.story {
  &__pictures {
    background-color: $color-primary;
    grid-column: full-start / col-end 4;
    background-image: linear-gradient(rgba($color-primary, .5), rgba($color-primary, 0.5)), 
                      url('../../public/img/back.jpg');
    background-size: cover;
    // background-position: 100%;

    display: grid;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: repeat(6, 1fr);
    align-items: center;

    @media only screen and (max-width: $bp-800){
      grid-column: 1 / -1;
      padding: 6rem;
    }
  }

  &__img--1 {
    width: 100%;
    grid-row: 2 / 6;
    grid-column: 2 / 6;
    box-shadow: var(--bshadow3);

    @media only screen and (max-width: $bp-800){
      grid-column: 1 / 5;
      grid-row: 1 / -1;
      // height: 100%;
      // width: 100%;
    }
    
  }
  
  &__img--2 {
    width: 115%;
    grid-row: 4/6;
    grid-column: 4/7;
    z-index: var(--z-index-1);
    box-shadow: var(--bshadow3);

    @media only screen and (max-width: $bp-800){
      grid-row: 1 / -1;
      width: 100%;
      transform: translateY(5rem)
    }
  }

  &__content {
    background-color: $color-grey-light-1;
    grid-column: col-start 5/ full-end;

    padding:6rem 8vw;

    @media only screen and (max-width: $bp-800){
      grid-column: 1 / -1;
      grid-row: 5 / 6;
      padding: 3rem 8vw;
    }

    //WITH FLEXBOX
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    // align-items: center;

    //WITH GRID
    display: grid;
    align-content: center;
    justify-items: center;
  }

  &__text {
    font-size: var(--font-s15);
    font-style: italic;
  }
}