.gallery {
  background-color: $color-grey-light-1;
  grid-column: full-start / full-end;
  position: relative;



  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(7, 5vw);
  grid-gap: 1.5rem;
  padding: 1.5rem;

  animation: moving 20s infinite linear;

  &:hover {
    animation: none;
  }
  
  &__item {
    &--1 {
      grid-row: 1/span 2;
      grid-column: 1 / span 2;

      transition: transform 1s;
      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--2 {
      grid-row: 1 / 4;
      grid-column: 3 / 6;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--3 {
      grid-row: 1 / 3;
      grid-column: 6 / 7;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--4 {
      grid-row: 1 / 3;
      grid-column: 7 / 9;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--5 {
      grid-row: 3 / 6;
      grid-column: 1 / 3;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--6 {
      grid-row: 4 / 6;
      grid-column: 3 / 5;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--7 {
      grid-row: 4 / 5;
      grid-column: 5 / 6;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--8 {
      grid-row: 3 / 5;
      grid-column: 6 / 8;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--9 {
      grid-row: 3 / 6;
      grid-column: 8 / 9;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--10 {
      grid-row: 6 / 8;
      grid-column: 1 / 2;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--11 {
      grid-row: 6 / 8;
      grid-column: 2 / 4;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--12 {
      grid-row: 6 / 8;
      grid-column: 4 / 5;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }

    &--13 {
      grid-row: 5 / 8;
      grid-column: 5 / 8;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }
    
    &--14 {
      grid-row: 6 / 8;
      grid-column: 8 / 9;
      transition: transform 1s;

      &:hover {
        transform: scale(1.5);
        z-index: 1;
      }
    }
    
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

}